import { itemsInCart } from "./items-in-cart";
import { createContext, useContext, useReducer } from "react";

const ProductContext = createContext();

const reducerFunc = (state, action) => {
  switch (action.type) {
    case "HIGHTOLOW":
      return {
        ...state,
        itemsInCart: state.itemsInCart.sort((a, b) => a.price - b.price)
      };

    case "LOWTOHIGH":
      return {
        ...state,
        itemsInCart: state.itemsInCart.sort((a, b) => b.price - a.price)
      };
    default:
      break;
  }
};

export const ProductProvider = ({ children }) => {
  const [stateProduct, dispatchProduct] = useReducer(reducerFunc, {
    itemsInCart
  });
  return (
    <ProductContext.Provider
      value={{ stateProduct, dispatchProduct, itemsInCart }}
    >
      {children}
    </ProductContext.Provider>
  );
};

export const useProduct = () => {
  return useContext(ProductContext);
};
