import "./style.css";
import { useProduct } from "../productContext";
// import { useState } from "react";
import { Card } from "./card";
export default function Products({ setRoute }) {
  const { stateProduct, dispatchProduct } = useProduct();

  return (
    <div className="content-page">
      <h1>Products</h1>
      <fieldset>
        <input
          type="radio"
          name="sort"
          onClick={() => dispatchProduct({ type: "LOWTOHIGH" })}
        />
        <label>Price High To Low</label>
        <input
          type="radio"
          name="sort"
          onClick={() => dispatchProduct({ type: "HIGHTOLOW" })}
        />
        <label>Price Low To High</label>
      </fieldset>

      <div className="product-showcase">
        {stateProduct.itemsInCart.map((item) => {
          return (
            <Card
              key={item.id}
              item={item}
              type="productShowcase"
              setRoute={setRoute}
            />
          );
        })}
      </div>
    </div>
  );
}
