import commerce from "./commerce.png";
import { useCart } from "../cartContext";

export const NavBar = ({ setRoute }) => {
  const { state } = useCart();
  // cartItems, wishlist
  return (
    <div>
      <div className="nav-box">
        <img className="nav-logo" src={commerce} alt="" />

        <ul className="navigation disable">
          <li>
            <a
              className="nav-btn"
              href="#Home"
              onClick={() => setRoute("Home")}
            >
              Home
            </a>
          </li>

          <li>
            <a
              className="nav-btn"
              href="#Products"
              onClick={() => setRoute("Products")}
            >
              Products
            </a>
          </li>

          <li>
            <a
              className="nav-btn"
              href="#Wishlist"
              onClick={() => setRoute("Wishlist")}
            >
              Wishlist
              <i className="value fas fa-heart">
                <span>{state.wishlist.length}</span>
              </i>
            </a>
          </li>

          <li>
            <a
              className="nav-btn"
              href="#Cart"
              onClick={() => setRoute("Cart")}
            >
              Cart
              <i className="value fas fa-shopping-cart">
                {" "}
                <span>
                  {state.cartItems.reduce(
                    (acc, value) => acc + value.quantity,
                    0
                  )}
                </span>
              </i>
            </a>
          </li>
        </ul>
        {/* ////////////////////////////////////////////////////// */}
        {/* <i class="fas fa-bars"></i> */}
      </div>
      <div className="mobile-nav-bar">
        <ul className="mob-nav-box">
          <li>
            <a
              // className="mob-nav-btn"
              href="#Home"
              onClick={() => setRoute("Home")}
            >
              <i class="mob-nav-btn fas fa-home"></i>
            </a>
          </li>

          <li>
            <a href="#Products" onClick={() => setRoute("Products")}>
              <i class="mob-nav-btn fas fa-box"></i>
            </a>
          </li>

          <li>
            <a href="#Wishlist" onClick={() => setRoute("Wishlist")}>
              <i className="mob-nav-btn value fas fa-heart">
                <span>{state.wishlist.length}</span>
              </i>
            </a>
          </li>

          <li>
            <a href="#Cart" onClick={() => setRoute("Cart")}>
              <i className="mob-nav-btn value fas fa-shopping-cart">
                <span>
                  {state.cartItems.reduce(
                    (acc, value) => acc + value.quantity,
                    0
                  )}
                </span>
              </i>
            </a>
          </li>
        </ul>
      </div>
    </div>
  );
};
