import "./card.css";
// import { Cart } from "./cart";
import { useCart } from "../cartContext";
// import { useState } from "react";

export const Card = ({ item, type, setRoute }) => {
  const {
    dispatch,
    ifPresentCart,
    ifPresentWishlist,
    cartHandler,
    wishListHandler
    // productHandler
  } = useCart();
  // const [move, setMove] = useState("Add to Cart");
  const productHandler = (item) => {
    const val = ifPresentCart(item.id);
    val ? setRoute("Cart") : dispatch({ type: "ADDTOCART", payload: item });
  };
  return (
    <div className="card-v" key={item.id}>
      <img src={item.image} width="100%" height="auto" alt={item.productName} />
      <div className="content-card">
        <div className="div1">
          <h4>{item.name}</h4>
          <p className="price">₹{item.price}</p>
        </div>

        {type === "productShowcase" && (
          <div className="div2">
            <p>{item.description}</p>
          </div>
        )}

        {type === "productShowcase" && (
          <div className="div3">
            {
              <button
                className="cart-btn btn-red"
                onClick={() => {
                  productHandler(item);
                }}
              >
                {ifPresentCart(item.id) ? "Go to Cart" : "Add to Cart"}
              </button>
            }
            {/* {move === "Added to Cart" && (
              <button
                className="cart-btn btn-red-disabled"
                disabled={ifPresentCart(item.id)}
              >
                {move}
              </button>
            )} */}
            <i
              onClick={
                ifPresentWishlist(item.id)
                  ? () => {
                      dispatch({
                        type: "REMOVEFROMWISHLIST",
                        payload: item.id
                      });
                    }
                  : () => {
                      dispatch({ type: "ADDTOWISHLIST", payload: item });
                    }
              }
              className="fa fa-heart"
              aria-hidden="true"
              style={{ color: ifPresentWishlist(item.id) ? "red" : "grey" }}
            ></i>
          </div>
        )}

        {type === "cart" && (
          <div className="div4">
            <div className="quantity-btn">
              <button
                onClick={() => {
                  dispatch({ type: "INCREMENT", payload: item.id });
                }}
              >
                +
              </button>
              {item.quantity}
              <button
                onClick={() => {
                  dispatch({
                    type: "DECREMENT",
                    payload: { id: item.id, quantity: item.quantity }
                  });
                }}
              >
                -
              </button>
            </div>
            <button
              className="cart-btn btn-blue"
              onClick={() => cartHandler(item)}
            >
              Move to Wishlist
            </button>
            <button
              className="cart-btn btn-red"
              onClick={() => dispatch({ type: "REMOVE", payload: item.id })}
            >
              Remove
            </button>
          </div>
        )}

        {type === "wishList" && (
          <div>
            <button
              className="cart-btn btn-blue"
              onClick={() => wishListHandler(item)}
            >
              Move to Cart
            </button>
            <button
              className="cart-btn btn-red"
              onClick={() =>
                dispatch({ type: "REMOVEFROMWISHLIST", payload: item.id })
              }
            >
              Remove
            </button>
          </div>
        )}
      </div>
    </div>
  );
};
